<template>

 <div class="swiper-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide" v-for='(item,index) in datalist' :key='index'><img :src="item.img" alt="">
        <span>{{item.city}}</span></div>
    </div>

   
  </div>

</template>

<script>

  import 'swiper/dist/css/swiper.css'

  import Swiper from 'swiper';

  export default {
    name: "Slider",
   props:{
    datalist:{
      type:Array,
      default:[]
    }
   },
    methods:{
    },
    mounted(){
      var that = this
      var swiper = new Swiper('.swiper-container', {
  	      slidesPerView: 'auto',
  	      centeredSlides: true,
  	      spaceBetween: 20,
  	      effect : 'coverflow',
  	      coverflowEffect: {
  	        rotate: 0,
  	        stretch: -20,
  	        depth: 300,
  	        modifier: 0.5,
  	        slideShadows : true
  	      },
          on:{
            slideChangeTransitionEnd:function(){
              that.$emit('getIndex',this.activeIndex)
            }
          }
  	    });

    }

   }

</script>

 

<style scoped>

  .swiper-container {
      width: 100%;
      height: 300px;
      margin:20px auto;
    }
    .swiper-container-coverflow .swiper-wrapper{
      position: relative;
      left: -0.6rem
    }
    .swiper-slide {
      text-align: center;
      font-size: 18px;
      background: #fff;
      position: relative;
      /* Center slide text vertically */
      display: -webkit-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      -webkit-justify-content: center;
      justify-content: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      -webkit-align-items: center;
      align-items: center;
      width: 60%;
      border-radius: 10px;
      overflow: hidden;
    }
    .swiper-slide img{
      width: 100%;
      height: 100%
    }
    .swiper-slide span{
      position: absolute;
      left: 0.1rem;
      bottom: 0.1rem;
      background: #fff;
      display: inline-block;
      border-radius: 0.1rem;
      padding: 0.05rem 0.1rem;
      font-size: 0.14rem
    }
    .swiper-slide:nth-child(2n) {
     border-radius: 10px
    }
    .swiper-slide:nth-child(3n) {
      border-radius: 10px
    }
    .swiper-container-3d .swiper-slide-shadow-left{
        border-radius: 10px
    }
    .swiper-container-3d .swiper-slide-shadow-right{
        border-radius: 10px
    }

</style>



